
.hmain{margin-top:5px;width:100%;overflow:hidden;
}
.htitle{width:100%;text-align:left
}
.htitle p span{width:10px;display:inline-block;margin-right:5px;
}
.htitle p{width:100%;color:orange;font-weight:normal;font-size:18px;height:30px
}
.hcont{width:100%;height:140px;text-align:left;padding-left:20px;position:relative;border-bottom:1px solid lightgray;margin-top:10px
}
.hcont .mleft{float:left;width:60%
}
.hcont .mleft h2{line-height:25px;font-size:18px
}
.hcont .mleft p{line-height:18px;margin:10px 0;color:gray;font-size:12px
}
.hcont .mright{float:right;position:absolute;right:20px;top:0
}
.hcont img{width:80px;margin-right:20px;display:inline-block
}



