
.fcont{border-top:15px solid #eee;
}
.ftitle{height:40px;margin-top:10px;padding:0 10px 0 10px ;
}
.fcont ul{overflow:hidden
}
.ftitle h2{float:left;line-height:30px;font-size:16px
}
.ftitle p{float:right;
}
.ftitle p a{font-size:12px;font-weight:normal;color:green;line-height:12px;font-size:15px
}
.fcont ul{padding: 0 20px ;height:130px
}
.fcont li{width:100px;float:left;margin-right:10px
}
.filmlist {width:100px;
}
.fcont li p{line-height:16px;font-size:14px;font-weight:bold
}
.fmain{width:100%;margin-top:10px;margin-bottom:10px
}
.fdetails{border-top:15px solid #eee
}



